@import url("https://fonts.googleapis.com/css?family=Ubuntu");
@import url("https://fonts.googleapis.com/css?family=Mukta");
@-webkit-keyframes navtogglein {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }
@keyframes navtogglein {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translateX(-5rem);
    transform: translateX(-5rem); }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

@keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translateX(-5rem);
    transform: translateX(-5rem); }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0); } }

html {
  font: 12px/1.5 'Ubunto', sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: 14px; } }

@media screen and (min-width: 600px) {
  html {
    font-size: 16px; } }

@media screen and (min-width: 800px) {
  html {
    font-size: 18px; } }

@media screen and (min-width: 1300) {
  html {
    font-size: 21px; } }

.site-title,
h1, h2, h3, h4, h5, h6 {
  font: 1em/1.2 'Mukta', sans-serif;
  margin: 1em 0 0.5em 0;
  padding: 0; }

h1 {
  font-size: 2.2rem; }

h2 {
  font-size: 2.0rem; }

h3 {
  font-size: 1.8rem; }

h4 {
  font-size: 1.6rem; }

h5 {
  font-size: 1.4rem; }

h6 {
  font-size: 1.2rem; }

p {
  margin: 0;
  padding-bottom: 1.5rem; }

ol, ul {
  margin: 0 0 1.5rem 0;
  padding: 0;
  margin-left: 1.5rem; }

ul li, ol li {
  list-style-position: inside; }

ul ul {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem; }

button, .button {
  display: inline-block;
  position: relative;
  outline: none;
  border: 0;
  text-decoration: none;
  padding: 0;
  font-size: 0.8rem;
  line-height: 1;
  cursor: pointer;
  background: transparent;
  color: #333;
  border: 2px solid #333;
  border-radius: 0.2rem;
  padding: 0.5rem 1rem;
  font-weight: bold;
  margin: 0 1rem 1rem 0; }

button:last-child, .button:last-child {
  margin-bottom: 0; }

button:hover,
button:focus,
.button:hover,
.button:focus {
  border-color: #060;
  color: #060;
  background: #FFF; }

button.fill, .button.fill {
  background: #333;
  color: #FFF; }

button.fill:hover,
button.fill:focus,
.button.fill:hover,
.button.fill:focus {
  border-color: #060;
  color: #FFF;
  background: #060; }

button span.fas, .button span.fas {
  display: inline-block;
  font-size: 0.9em;
  margin-right: 0.3rem; }

form {
  max-width: 30rem;
  margin: 0 auto; }

form,
form fieldset,
form legend,
form label,
form input,
form textarea,
form select {
  display: block; }

form fieldset,
form legend {
  border: 1px solid #666;
  background: #F9F9F9;
  border-radius: 0.2rem;
  -webkit-box-shadow: 0 0 1rem 0.1rem rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 1rem 0.1rem rgba(0, 0, 0, 0.1); }

form fieldset {
  padding: 2.5em 2em 2em 2em;
  position: relative; }

form legend {
  width: 30%;
  padding: 0.5rem 1rem;
  left: -1px;
  top: -1.4rem;
  position: absolute; }

form label {
  font-weight: bold; }

.field .input {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center; }

.field .input:after {
  display: block;
  content: ''; }

.field .input input {
  border: none;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 0.8rem;
  outline: none;
  background: transparent;
  z-index: 2;
  -webkit-box-ordinal-group: 3;
  -ms-flex-order: 2;
  order: 2; }

.field .input .fas {
  z-index: 2;
  position: relative;
  padding: 0 0 0 0.8rem;
  font-size: 0.9em;
  -webkit-box-ordinal-group: 2;
  -ms-flex-order: 1;
  order: 1; }

.field .input input + .actions {
  display: block;
  background: #FFF;
  border: 2px solid #333;
  border-radius: 0.2rem;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1; }

.field .input input:focus + .actions {
  border-color: #090; }

.field .input input:focus + .actions + .fas {
  color: #090; }

@media screen and (min-width: 800px) {
  .field {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }
  .field .input {
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto; }
  .field .input + * {
    margin-left: 0.5rem; }
  .field label {
    line-height: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-right: 0.5rem; } }

body {
