@keyframes navtogglein {
    from {
        opacity: 0;
        transform: translate(0, -2rem);
    }

    to {
        opacity: 1;
        transform: translate(0, 0);
    }
}


@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateX(-5rem);
      }
      
      to {
        opacity: 1;
        transform: translateX(0);
      }
}